<template>
	<div id="store-goods" class="theme-card">
		<template v-if="template == 4 || template == 5">
			<follow :follow="follow" v-if="follow && follow.status == 1"></follow>
			<public-header :title="store.title" v-if="!isWeixin"></public-header>
			<div class="container" :class="{'has-child-4': categorySelected && categorySelected.child && categorySelected.child.length > 0 && categorySelected.child.length <= 4, 'has-child-8': categorySelected && categorySelected.child && categorySelected.child.length > 4}">
				<div class="category-container bg-default">
					<van-swipe
						:width="90"
						:height="97"
						:show-indicators="false"
						:loop="false"
					>
						<van-swipe-item
							class="cate-item"
							:class="{active: item.id == categorySelectedId}"
							v-for="(item, index) in categoryAll" :key="item.id"
							@click="onToggleCategory(item.id, index, '')"
						>
							<div class="img-wrap">
								<img class="img-100" :src="item.thumb" alt="">
							</div>
							<div class="cate-title ellipsis">{{item.title}}</div>
						</van-swipe-item>
					</van-swipe>
					<template v-if="categorySelected && categorySelected.child && categorySelected.child.length > 0">
						<template v-if="categorySelected.child_length <= 8">
							<div class="category-children van-hairline--top">
								<div class="cate-child-item"
									v-for="(item, index) in categorySelected.child"
									:key="item.id"
									@click="onToggleCategory(item.id, index, 'child')"
								>
									<div class="child-inner ellipsis" :class="{active: item.id == childSelectedId}">{{item.title}}</div>
								</div>
							</div>
						</template>
						<template v-else>
							<van-swipe
								:height="97"
								:show-indicators="true"
								:loop="true"
								indicator-color="#ff2d4b"
								class="w-100"
							>
								<template v-for="page in categorySelected.child_page">
									<van-swipe-item class="category-swipe-item">
										<template v-for="(item, index) in categorySelected.child">
											<template v-if="index >= (page-1) * 8 && index < page * 8">
												<div class="cate-child-item" @click="onToggleCategory(item.id, index, 'child')"
												>
													<div class="child-inner ellipsis" :class="{active: item.id == childSelectedId}">{{item.title}}</div>
												</div>
											</template>
										</template>
									</van-swipe-item>
								</template>
							</van-swipe>
						</template>
					</template>
				</div>
				<router-link tag="div" :to="util.getUrl({path: '/plugin/pages/kabao/detail', query: {sid: store.id}})" class="kabao-wrap" v-if="store.kabao_status == 1">
					<div class="left">
						<div class="logo">
							<img class="img-100" :src="store.logo" alt="">
						</div>
						<div class="margin-5-l flex" v-if="member_kabao.group_id > 0">
							<span>{{member_kabao.group_title}}</span>
						</div>
						<div class="margin-5-l" v-else>商家会员</div>
					</div>
					<div class="c-gray right">
						<span>{{member_kabao.group_id > 0 ? '查看权益' : '免费入会享受会员权益'}}</span>
						<i class="icon icon-right font-12"></i>
					</div>
				</router-link>
				<!-- 商家代金券 -->
				<div class='coupon-show-container' v-if="coupon.id > 0 && couponStatus == 1">
					<div class='coupon-price'>
						{{Lang.dollarSign}}<div>{{coupon.price}}</div>
					</div>
					<div class='coupon-detail'>
						<div class='coupon-title'>商家代金券</div>
						<div class='coupon-desc'>内含{{coupon.num}}张券</div>
					</div>
					<div class='coupon-fetch' @click="onCollectCoupon">去领取</div>
				</div>
				<!-- 门店海报 -->
				<div class="banner" v-if="shopPage">
					<template v-for="(shopPageItem, shopPageKey) in shopPage">
						<div class="banner-item" v-if="shopPageItem.wxapp_link"  @click="util.jsUrl(shopPageItem.wxapp_link)" >
							<img :src="shopPageItem.thumb">
						</div>
						<router-link class="banner-item" v-else-if="shopPageItem.goodsLength == 1" :to="util.getUrl({path: '/pages/store/goodsDetail', query:{sid: store.id, id: shopPageItem.goods[0], pindan_id: pindan_id}})">
							<img :src="shopPageItem.thumb">
						</router-link>
						<router-link class="banner-item" v-else-if="shopPageItem.goodsLength > 1" :to="util.getUrl({path: '/core/pages/store/shopPage', query:{sid: store.id, shopPageKey: shopPageKey, pindan_id: pindan_id}})">
							<img :src="shopPageItem.thumb">
						</router-link>
					</template>
				</div>
				<van-list
						class="goods-container"
						v-model="goodsLoading"
						:finished="goodsItem.loaded"
						@load="onGetGoods"
						:offset="50"
				>
					<template v-if="template == 5">
						<!--单列-->
						<div class="diy-goods-single-row">
							<div class="goods-item" v-for="item in goodsItem.data" :key="item.id">
								<router-link class="goods-img" tag="div" :to="util.getUrl({path: '/pages/store/goodsDetail', query: {id: item.id, sid: store.id, pindan_id: pindan_id}})">
									<img v-lazy="{src: item.thumb, loading: lazyload_goods}">
									<div class="discount hide" v-if="item.unitnum > 1">{{item.unitnum_multi_cn}}购</div>

								</router-link>
								<router-link class="goods-info" tag="div" :to="util.getUrl({path: '/pages/store/goodsDetail', query: {id: item.id, sid: store.id, pindan_id: pindan_id}})">
									<div>
										<div class="goods-name">{{item.title}}</div>
										<div class="sale-num">已售:{{item.sailed}} 好评率:{{item.comment_good_percent}}</div>
										<div class="svip-price-tips" v-if="item.svip_status == 1 && item.discount && !item.discount_price">
											<div class="svip-price">
												<i class="icon icon-crownfill"></i>
												<span>{{item.discount}}折</span>
											</div>
											<div class="svip-activity">超级会员专享</div>
											<div class='discount margin-10-l' v-if="item.unitnum > 1">
												<van-icon name='tag'></van-icon>{{item.unitnum_multi_cn}}购
											</div>
										</div>
										<div class='discount'  v-else>
											<template v-if="item.discount">
												<van-icon name='tag'></van-icon>
												{{item.discount}}折 限{{item.max_buy_limit}}份 {{item.unitnum > 1 ? item.unitnum_multi_cn + '购' : ''}}
											</template>
											<template v-else-if="item.unitnum > 1">
												<van-icon name='tag'></van-icon>{{item.unitnum_multi_cn}}购
											</template>
										</div>
									</div>
									<div>
										<div class="goods-price">
											<div class="in-b now-price">{{Lang.dollarSign}}{{item.price}}<span class="font-14">{{item.unitname_cn}}</span></div>
											<div class="in-b old-price" v-if="item.old_price > 0">{{Lang.dollarSign}}{{item.old_price}}</div>
										</div>
										<div class="kabao-price-wrap margin-5-b" v-if="item.kabao_status == 1 && !item.discount_price">
											<span class="kabao-price">
												<span class="font-12">{{Lang.dollarSign}}</span>{{item.kabao_price}}
												<span class="font-14">{{item.unitname_cn}}</span>
											</span>
											<span class="kabao-label">
												<i class="icon icon-vip"></i>
												<span>{{item.kabao_group_title}}</span>
											</span>
										</div>
									</div>
								</router-link>
								<goods-handle
										:goods="item"
										:optionId="0"
										from="list"
										@onParentPlus="onParentPlus"
										@onParentSelectOption="onParentSelectOption"
										@onParentPopupNotInSailTime="onParentPopupNotInSailTime"
										@showMoveDot="showMoveDotFun"
								>
								</goods-handle>
							</div>
						</div>
					</template>
					<template v-else-if="template == 4">
						<!--双列-->
						<div class="diy-goods-double-row">
							<div class="goods-item" v-for="item in goodsItem.data" :key="item.id">
								<div class="goods-item-inner">
									<router-link tag="div" :to="util.getUrl({path: '/pages/store/goodsDetail', query: {id: item.id, sid: store.id, pindan_id: pindan_id}})" class="goods-img">
										<img v-lazy="{src: item.thumb, loading: lazyload_goods}">
										<div class="discount" v-if="(item.svip_status != 1 || item.discount_price > 0) && item.discount > 0">
											<div>
												<van-icon name="tag"></van-icon> {{item.discount}}折 限{{item.max_buy_limit}}份 {{item.unitnum > 1 ? item.unitnum_multi_cn + '' : ''}}
											</div>
										</div>
										<div class="discount" v-if="item.unitnum > 1 && !item.discount_price">
											<div>
												<van-icon name="tag"></van-icon> {{item.unitnum_multi_cn}}
											</div>
										</div>
									</router-link>
									<router-link tag="div" :to="util.getUrl({path: '/pages/store/goodsDetail', query: {id: item.id, sid: store.id, pindan_id: pindan_id}})" class="goods-info">
										<div class="info-top">
											<div class="goods-name">{{item.title}}</div>
											<div class="goods-content" v-if="item.content">{{item.content}}</div>
											<div class="svip-price-tips" v-if="item.svip_status == 1 && item.discount && !item.discount_price">
												<div class="svip-price">
													<span class="ellipsis">{{item.discount}}折</span>
												</div>
												<div class="svip-activity ellipsis">超级会员专享</div>
											</div>
											<div class="kabao-price-wrap margin-5-b" v-else-if="item.kabao_status == 1 && !item.discount_price">
												<span class="kabao-price">
													<span class="font-12">{{Lang.dollarSign}}</span>{{item.kabao_price}}
													<span class="font-14">{{item.unitname_cn}}</span>
												</span>
												<span class="kabao-label">
													<i class="icon icon-vip"></i>
													<span>{{item.kabao_group_title}}</span>
												</span>
											</div>
											<div class="sale-num" v-else>已售:{{item.sailed}} 好评率:{{item.comment_good_percent}}%</div>
										</div>
										<div class="goods-price">
											<div class="now-price">{{Lang.dollarSign}}{{item.price}}<span class="font-14">{{item.unitname_cn}}</span></div>
											<div class="old-price margin-5-t" v-if="item.old_price > 0">{{Lang.dollarSign}}{{item.old_price}}</div>
										</div>
									</router-link>
									<goods-handle
											:goods="item"
											:optionId="0"
											from="list"
											@onParentPlus="onParentPlus"
											@onParentSelectOption="onParentSelectOption"
											@onParentPopupNotInSailTime="onParentPopupNotInSailTime"
											@showMoveDot="showMoveDotFun"
									>
									</goods-handle>
								</div>
							</div>
						</div>
					</template>
					<load v-if="goodsItem.empty == 1" type="loaded" text="暂无商品" bgcolor="#f5f5f5"></load>
					<load v-else-if="goodsItem.loaded" type="loaded" text="已经到底了" bgcolor="#f5f5f5"></load>
				</van-list>
			</div>
		</template>
		<template v-else>
			<store-header :store="store" :pindan_id="pindan_id" :newStyle="true" :class="{simple: headerHide}"></store-header>
			<div :class="{'store-container': 1, fixed: headerHide}">
				<van-tabs class="store-tabs"
					v-model="pageActive"
					swipeable
					sticky
					:offset-top="44"
					@disabled="util.jsUrl('/core/pages/store/pindan', {sid: store.id, pindan_id: pindan_id})"
				>
					<van-tab title="点菜">
						<!--按分类加载-->
						<template v-if="template_page == 1">
							<van-row class="goods-container">
								<van-col span="6" class="category-container" :style="{height: height.goodsWrapHeight + 'px', 'overflow-y': 'hidden'}">
									<div ref="categoryWrapper" style="height: 100%;">
										<van-sidebar :active-key="categorySelectedIndex">
											<template v-for="(item, index) in categoryAll">
												<van-sidebar-item class="border-0px" :title="item.title" @click="onToggleCategory(item.id, index, '')" v-if="item.total > 0" :info="item.total" />
												<van-sidebar-item class="border-0px" :title="item.title" @click="onToggleCategory(item.id, index, '')" v-else />
											</template>
										</van-sidebar>
									</div>
								</van-col>
								<van-col span="6" class="van-col-empty"></van-col>
								<van-col span="18" class="goods-wrap" style="min-height: 100vh">
									<van-list v-model="goodsLoading" :finished="goodsItem.loaded" @load="onGetGoods" :offset="50">
										<template v-if="categorySelected.child">
											<div class="tag2-list" v-if="categorySelected.child.length > 0">
												<div class="tag2-item" v-for="(item, index) in categorySelected.child" :class="{selected: index == childSelectedIndex}" @click="onToggleCategory(item.id, index, 'child')">
													{{item.title}}
													<div :class="{dot: item.total}" v-if="item.total > 0"></div>
												</div>
											</div>
										</template>
										<div class="category-title">
											<span>{{categorySelected.child && categorySelected.child.length > 0 ? categorySelected.child[childSelectedIndex].title : categorySelected.title}}</span>
											<span class="limit-tip" v-if="categorySelected.min_fee > 0">最低消费{{categorySelected.min_fee}}{{Lang.dollarSignCn}}</span>
											<div class="title-rank">
												<div class="sales " :class="{'selected': (goodsItem.orderby.type == 'sailed')}" @click="onOrderby('sailed', 'desc')">销量</div>
												<div class="shu">|</div>
												<div class="prionToggleCategoryce-container" @click="onOrderby('price', (goodsItem.orderby.value == 'desc' ? 'asc' : 'desc'))">
													<div class="price">价格</div>
													<div class="triangle">
														<div class="triangle-top" :class="{selected: (goodsItem.orderby.type == 'price' && goodsItem.orderby.value == 'asc')}"></div>
														<div class="triangle-bottom " :class="{selected: (goodsItem.orderby.type == 'price' && goodsItem.orderby.value == 'desc')}"></div>
													</div>
												</div>
											</div>
										</div>
										<template v-if="goodsItem.data.length > 0">
											<template v-for="(item, index) in goodsItem.data">
												<template v-if="template == 2">
													<div class="goods-item-1" @click="util.jsUrl('/pages/store/goodsDetail', {id: item.id, sid: store.id, pindan_id: pindan_id})">
														<div class="thumb">
															<img :src="item.thumb" alt="">
															<div class="label"></div>
														</div>
														<div class="info">
															<div class="w-100">
																<div class="goods-title">{{item.title}}</div>
																<div class="description" v-if="item.content">{{item.content}}</div>
																<div class="sold">
																	已售{{item.sailed}}
																	<span v-if="item.comment_good_percent > 0">
																		好评{{item.comment_good_percent}}
																	</span>
																</div>
																<div class="svip-price-tips" v-if="item.svip_status == 1 && item.discount && !item.bargain_id">
																	<div class="svip-price">
																		<span>{{item.discount}}折</span>
																	</div>
																	<div class="svip-activity">超级会员专享</div>
																</div>
																<div class="kabao-price-wrap margin-5-b" v-else-if="item.kabao_status == 1 && !item.bargain_id">
																	<span class="kabao-price">
																		<span class="font-12">{{Lang.dollarSign}}</span>{{item.kabao_price}}
																		<span class="font-14">{{item.unitname_cn}}</span>
																	</span>
																	<span class="kabao-label">
																		<i class="icon icon-vip"></i>
																		<span>{{item.kabao_group_title}}</span>
																	</span>
																</div>
															</div>
															<div class="w-100">
																<div class="price">
																	<div class="now-price">
																		<span class="price-icon">{{Lang.dollarSign}}</span><span class="font-bold">{{item.price}}</span><span class="font-14" v-if="0">{{item.unitname_cn}}</span>
																	</div>
																	<div class='old-price' v-if="item.old_price">
																		<span class="price-icon">{{Lang.dollarSign}}</span>{{item.old_price}}
																	</div>
																	<goods-handle
																		:goods="item"
																		:optionId="0"
																		from="list"
																		@onParentPlus="onParentPlus"
																		@onParentSelectOption="onParentSelectOption"
																		@showMoveDot="showMoveDotFun"
																	>
																	</goods-handle>
																</div>
																<div class='discount' v-if="item.svip_status != 1 || item.bargain_id > 0">
																	<template v-if="item.discount">
																		<van-icon name='tag'></van-icon>
																		{{item.discount}}折 限{{item.max_buy_limit}}份 {{item.unitnum > 1 ? item.unitnum_multi_cn + '购' : ''}}
																	</template>
																	<template v-else-if="item.unitnum > 1">
																		<van-icon name='tag'></van-icon>{{item.unitnum_multi_cn}}购
																	</template>
																</div>
																<div class='discount' v-else-if="item.unitnum > 1">
																	<van-icon name='tag'></van-icon>{{item.unitnum_multi_cn}}购
																</div>
															</div>
														</div>
													</div>
												</template>
												<template v-else-if="template == 1">
													<div class="goods-item-2" @click="util.jsUrl('/pages/store/goodsDetail', {id: item.id, sid: store.id, pindan_id: pindan_id})">
														<div class="inner van-hairline--bottom van-hairline--right">
															<div class="thumb">
																<img class="goods-img" :src="item.thumb" alt="">
																<div class="discount" v-if="(item.svip_status != 1 || item.bargain_id > 0) && item.discount > 0">
																	<div>
																		<van-icon name="tag"></van-icon> {{item.discount}}折 限{{item.max_buy_limit}}份 {{item.unitnum > 1 ? item.unitnum_multi_cn + '' : ''}}
																	</div>
																</div>
																<div class="discount" v-if="item.unitnum > 1 && !item.bargain_id">
																	<div>
																		<van-icon name="tag"></van-icon> {{item.unitnum_multi_cn}}
																	</div>
																</div>
																<div class="label" v-if="item.label">{{item.label}}</div>
															</div>
															<div class="goods-title">{{item.title}}</div>
															<div class="svip-price-tips" v-if="item.svip_status == 1 && !item.bargain_id && item.discount">
																<div class="svip-price">
																	<span class="ellipsis">{{item.discount}}折</span>
																</div>
																<div class="svip-activity ellipsis">超级会员</div>
															</div>
															<div class="sold" v-else>
																已售{{item.sailed}}
																<div class="margin-5-l">好评率{{item.comment_good_percent}}</div>
															</div>
															<div class="kabao-price-wrap" v-if="member_kabao.vip_goods == 1">
																<template v-if="item.kabao_status == 1 && !item.bargain_id">
																	<div class="font-12">{{Lang.dollarSign}}</div>
																	<div class="kabao-price">{{item.kabao_price}}</div>
																	<div class="kabao-label">
																		<span class="icon icon-vip"></span>
																		<span>{{item.kabao_group_title}}</span>
																	</div>
																</template>
															</div>
															<div class="price">
																<div class="font-12">{{Lang.dollarSign}}</div>
																<div class="font-18 font-bold">{{item.price}}</div>
																<div class="font-12">{{item.unitname_cn}}</div>
																<goods-handle
																		:goods="item"
																		:template="template"
																		:optionId="0"
																		from="list"
																		templatePage="1"
																		@onParentPlus="onParentPlus"
																		@onParentSelectOption="onParentSelectOption"
																		@onParentPopupNotInSailTime="onParentPopupNotInSailTime"
																		@showMoveDot="showMoveDotFun"
																>
																</goods-handle>
															</div>
														</div>
													</div>
												</template>
												<template v-else-if="template == 3">
													<div class="goods-item-3" @click="util.jsUrl('/pages/store/goodsDetail', {id: item.id, sid: store.id, pindan_id: pindan_id})">
														<div class="thumb">
															<img class="goods-img" :src="item.thumb" alt="">
															<div class="label" v-if="item.label">{{item.label}}</div>
															<div class="sell-info">
																<span>已售{{item.sailed}}</span>
																<span>好评率{{item.comment_good_percent}}</span>
															</div>
															<div class="discount" v-if="(item.svip_status != 1 || item.bargain_id > 0) && item.discount > 0">
																<div>
																	<van-icon name="tag"></van-icon> {{item.discount}}折 限{{item.max_buy_limit}}份 {{item.unitnum > 1 ? item.unitnum_multi_cn + '' : ''}}
																</div>
															</div>
															<div class="discount" v-if="item.unitnum > 1 && !item.bargain_id">
																<div>
																	<van-icon name="tag"></van-icon> {{item.unitnum_multi_cn}}
																</div>
															</div>
														</div>
														<div class="padding-10">
