											<div class="flex">
												配送服务:
												<div class='allocation-icon' v-if="store.delivery_title">{{store.delivery_title}}</div>
												<span class='inline' v-else>由 商家 </span>
												提供配送服务
											</div>
										</van-col>
									</van-row>
								</van-cell>
								<van-cell>
									<van-row class="flex">
										<van-col span="2">
											<div class="icon icon-time font-18"></div>
										</van-col>
										<van-col span="22">
											<div class="flex">
												营业时间:&nbsp;&nbsp;
												<span>{{store.business_hours_cn}}</span>
											</div>
										</van-col>
									</van-row>
								</van-cell>
								<template v-if="store.sns">
									<van-cell v-if="store.sns.qq">
										<van-row class="flex">
											<van-col span="2">
												<div class="icon icon-qq font-18"></div>
											</van-col>
											<van-col span="22">
												<div @click="util.jsUrl('http://wpa.qq.com/msgrd?v=3&uin=' + store.sns.qq + '&site=qq&menu=yes')">{{store.sns.qq}}</div>
											</van-col>
										</van-row>
									</van-cell>
									<van-cell v-if="store.sns.weixin">
										<van-row class="flex">
											<van-col span="2">
												<div class="icon icon-weixin font-18"></div>
											</van-col>
											<van-col span="22">{{store.sns.weixin}}</van-col>
										</van-row>
									</van-cell>
								</template>
							</van-cell-group>
							<div class="store-activity" v-if="store.activity && store.activity.items">
								<van-row class="activity-item" v-for="(item, index) in store.activity.items" :key="index">
									<van-col span="1">
										<div class="icon-b">
											<img :src="'static/img/' + item.type + '_b.png'">
										</div>
									</van-col>
									<van-col span="23" class="padding-10-l ellipsis">
										{{item.title}}
									</van-col>
								</van-row>
							</div>
							<div class="padding-15 text-center bg-default margin-10-tb" @click="util.jsUrl('/core/pages/store/report', {sid: store.id})">举报商家</div>
						</div>
					</van-tab>
					<van-tab disabled v-if="store.pindan_status == 1 && pindan_id == 0">
						<template #title>
							<div class="pindan-invite">邀请拼单</div>
						</template>
					</van-tab>
				</van-tabs>
			</div>
		</template>



		<van-popup v-model="popupNotInSailTime" class="not-in-time-popup" v-if="popupNotInSailTime">
			<div class='popup-top'>
				<div class='popup-item' v-if="goodsActive.week_cn">
					<div class='popup-title'>每周可售日期</div>
					<div class='popup-text'>{{goodsActive.week_cn}}</div>
				</div>
				<div class='popup-item' v-if="goodsActive.time_cn">
					<div class='popup-title'>每天可售时间</div>
					<div class='popup-text'>
						{{goodsActive.time_cn}}
					</div>
				</div>
			</div>
			<div class='popup-bottom border-1px-t' @click="popupNotInSailTime = false">知道了</div>
		</van-popup>

		<van-popup v-model="ParentPopupSpecShow" class="options-popup" v-if="ParentPopupSpecShow">
			<div class="options-dialog">
				<div class="muti-close" @click="ParentPopupSpecShow = false"></div>
				<div class="muti-wrap">
					<div class="muti-food-title border-1px-b">
						{{goodsActive.title}}
					</div>
					<div class="muti-cont">
						<div class='muti-cont-inner'>
							<div class='muti-sec first' v-if="goodsActive.is_options == 1">
								<div class='muti-sec-title'>规格</div>
								<div class='muti-choice'>
									<template v-for="(item, index) in goodsActive.options">
										<div :class="{selected: (goodsActive.activeOptions.optionSelected == item.id)}" @click="onParentToggleOption(item.id, '', 'option')">{{item.name}}</div>
									</template>
								</div>
							</div>
							<div class='muti-sec' v-if="goodsActive.is_attrs == 1">
								<template v-for="(attrItem, attrIndex) in goodsActive.attrs">
									<div class='muti-sec-title'>{{attrItem.name}}</div>
									<div class='muti-choice'>
										<template v-for="(label, labelIndex) in attrItem.label">
											<div :class="{selected: (goodsActive.activeOptions.attrsSelected[attrIndex] == labelIndex)}" @click="onParentToggleOption(attrIndex, labelIndex, 'attr')">{{label}}</div>
										</template>
									</div>
								</template>
							</div>
						</div>
					</div>
					<div class="muti-choose">
						<div class='muti-oprt'>
							<div class='muti-cart-oprt clearfix'>
								<div class='add-food'>
									<van-icon name='plus' @click="onParentPlus($event, goodsActive,  '', '', '1')" style="background-color: #6BBA50;"></van-icon>
								</div>
								<template v-if="goodsSpecNum">
									<div class='food-num'>{{goodsSpecNum}}</div>
									<div class='remove-food'>
										<div class='remove icon icon-minus' @click="onParentMinus(goodsActive)"></div>
									</div>
								</template>
							</div>
						</div>
						<div class='muti-info'>
							<div class='muti-price'>{{Lang.dollarSign}}{{goodsActive.activeOption.price}}</div>
							<div class='muti-old-price' v-if="goodsActive.activeOption.origin_price">
								{{Lang.dollarSign}}{{goodsActive.activeOption.origin_price}}
							</div>
							<div class='muti-type'>({{goodsActive.activeOption.name}})</div>
						</div>
					</div>
				</div>
			</div>
		</van-popup>

		<!-- 购物车部分 -->
		<store-cart
			:show="activetabs == 0"
			:store="store"
			:cart="cart"
			:pindan_id="pindan_id"
			@selectPinadan="selectPinadan"
			v-if="!pageActive"
		>
		</store-cart>
		<transition name="loading">
			<iloading v-if="preLoading"></iloading>
		</transition>
		<!--小球-->
		<transition
			appear
			@after-appear = 'onParentAfterEnter'
			@before-appear="onParentBeforeEnter"
			v-for="(item,index) in showMoveDot"
			:key="index"
		>
			<div class="parent_move_dot" v-if="item"></div>
		</transition>
		<!--公告弹出层-->
		<notice-popup v-if="store.tips && showNotice" :tips="store.tips"></notice-popup>
		<!--普通用户购买超级会员商品弹出层-->
		<van-popup v-model="popupBuySvip" class="buy-svip">
			<div class='popup-svip-title'>客官，专享价限超级会员享受哦</div>
			<div class="svip-img">
				<div class="svip-vs-left">
					<i class="icon icon-crownfill"></i>
					<div class="font-12">会员专享价</div>
					<div class="svip-vs-price"><span class="font-14">{{Lang.dollarSign}}</span>{{goodsActive.svip_price}}</div>
				</div>
				<div class="svip-vs-right">
					<div class="svip-putong-title">普通用户价格</div>
					<div class="svip-putong-price"><span class="font-14">{{Lang.dollarSign}}</span>{{goodsActive.origin_price}}</div>
				</div>
			</div>
			<div class="buy-svip-text">即刻加入超级会员，尊享众多会员优惠</div>
			<button class="svip-price-btn font-16 margin-15-t">
				<div class="font-bold" @click="onParentPlus($event, goodsActive, '', '', '3')">会员专享价购买</div>
				<div class="font-12 padding-5-t">(订单结算时成为会员享优惠)</div>
			</button>
			<van-button size="normal" class="price-btn font-16 margin-15-t" @click="onParentPlus($event, goodsActive, '', '', '2')">原价购买</van-button>
		</van-popup>

		<!--快捷导航-->
		<template v-if="store.menu && store.menu.path && store.menu.path.goods == 1">
			<navigator :menu="menu" :showShare="true" @onToggleSharePopupStatus="onToggleSharePopupStatus"></navigator>
		</template>
		<poster-popup
			:status="sharePopupStatus"
			:query="{type: 2, id: store.id}"
			@onTogglePopup="onToggleSharePopupStatus"
		>
		</poster-popup>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicFooter from '@/components/footer'
	import PublicHeader from '@/components/header'
	import Load from '@/components/load'
	import StoreCart from '@/components/storeCart'
	import GoodsHandle from '@/components/goodsHandle'
	import StoreHeader from '@/components/storeHeader'
	import noticePopup from '@/components/noticePopup'
	import follow from '@/components/follow'
	import BScroll from 'better-scroll'
	import navigator from '@/components/navigator'
	import PosterPopup from '@/components/PosterPopup'
	import {getData} from "@/controller/funcCommon"
	export default {
		name: 'StoreGoods',
		data() {
			return {
				code: 0,
				goodsActive: {},
				popupNotInSailTime: false,
				ParentPopupSpecShow: false,
				preLoading: true,
				goodsLoading: true,
				store:{
					is_rest: 0,
					activity: {
						num: 0
					}
				},
				member_kabao: {
					group_id: 0,
					group_title: '',
				},
				activetabs: 0,
				swiper_dots: false,
				tabswiperable: true,
				is_rest: 0,
				status: {
					options_show: false,
					not_in_time_show: false,
					cart_show: false,
				},
				vanColHeight: 0,
				commentHeight: 0,
				categorySelectedIndex: 0,
				categorySelectedId: 0,
				categoryAll:[],
				categorySelected: {
					child: [{}]
				},
				childSelectedIndex: 0,
				childSelectedId: 0,
				goodsAll: [],
				goodsItem: {
					orderby: {
						type: '',
						value: ''
					}
				},
				recommend_stores: {},
				coupon: [],
				shopPage: {},
				cart: {
					num: 0,
				},
				template: 2,
				couponStatus: 1,
				lazyload_goods: '',
				showMoveDot: [],
				windowHeight: 0,
				showNotice: true,

				cateHasGoods: [],
				template_page: 0,
				scrollY: 0,
				listHeight: [],
				tabActive: 0,
				popupBuySvip: false,
				follow: {
					status: 0
				},
				menu: {
					css: {},
					params: {},
					data: {},
					position: {
						left: '15px',
						bottom: '80px',
						right: 'inherit'
					},
				},
				sharePopupStatus: false,

				stat: {},
				comments: {
					page: 1,
					psize: 15,
					loading: true,
					finished: false,
					empty: false,
					data:[]
				},
				commentActiveType: 0,
				note: 1,
				pageActive: 0,
				headerHide: false,
				height: {
					storeHeader: 0,
					headerBar: 0,
					storeTabs: 0,
					goodsWrapHeight: 0,
					goodsList: []
				}
			}
		},
		components: {
			PublicHeader,
			PublicFooter,
			Load,
			StoreCart,
			GoodsHandle,
			StoreHeader,
			noticePopup,
			follow,
			navigator,
			PosterPopup
		},
		methods: {
			...mapMutations([
				'replaceStore', 'replaceCart', 'replaceState'
			]),

			onGetComment(force) {
				var that = this;
				if(force === true) {
					that.comments = {
						page: 1,
						psize: 15,
						loading: true,
						finished: false,
						empty: false,
						data:[]
					}
				}
				if(that.comments.finished) {
					return false;
				}
				var params = {
					sid: that.sid,
					note: that.note,
					type: that.commentActiveType,
					page: that.comments.page,
					psize: that.comments.psize,
				};
				getData({
					vue: that,
					url: 'wmall/store/goods/comment',
					data: params,
					success: (res) => {
						var comments = res.comments;
						that.comments.data = [...that.comments.data, ...comments];
						if(!that.comments.data.length) {
							that.comments.empty = true;
						}
						if(comments && comments.length < that.comments.psize ) {
							that.comments.finished = true;
						}
						that.comments.page++;
						that.comments.loading = false;
					}
				})
			},

			onCheckedReadComment() {
				var that = this;
				if(that.note == 1) {
					that.note = 0;
				} else {
					that.note = 1;
				}
				that.onGetComment(true);
			},

			onChangeCommentType(type) {
				var that = this;
				if(that.commentActiveType == type) {
					return;
				}
				that.commentActiveType = type;
				that.onGetComment(true);
			},

			_calculateHeight() {
				var that = this;
				that.windowHeight = window.innerHeight;

				var storeHeaderDom = document.getElementById('store-header-new');
				if(storeHeaderDom) {
					that.height.storeHeader = storeHeaderDom.clientHeight;
				}
				var storeTabsDom = document.getElementsByClassName('van-tabs__wrap')[0];
				if(storeTabsDom) {
					that.height.storeTabs = storeTabsDom.clientHeight;
				}
				var headerBarDom = document.getElementsByClassName('store-bar')[0];
				if(headerBarDom) {
					that.height.headerBar = headerBarDom.clientHeight;
				}
				that.height.goodsWrapHeight = that.windowHeight - that.height.storeHeader - that.height.headerBar;
				if(that.template_page == 0) {
					var goodsListDom = document.getElementsByClassName('goods-list');
					if(goodsListDom && goodsListDom.length > 0) {
						that.height.goodsList.push(0);
						var goodsListHeight = that.height.storeHeader - that.height.headerBar;
						for(let i = 0; i < goodsListDom.length; i++) {
							let item = goodsListDom[i];
							goodsListHeight += item.clientHeight;
							if(that.template > 1) {
								//单列加上每个goods-list底部margin-15
								goodsListHeight += 15;
							}
							that.height.goodsList.push(goodsListHeight);
						}
					}
				}
			},

			_followScroll(index) {
				var that = this;
				var categoryItem = document.getElementsByClassName('van-sidebar-item');
				let el = categoryItem[index];
				that.categoryScroll.scrollToElement(el, 300, 0, -100);
			},

			onPageScroll() {
				var that = this;
				that._calculateHeight();
				that.categoryScroll = new BScroll(that.$refs.categoryWrapper, {
					click: true
				});
				console.log(111);
				window.addEventListener('scroll', function() {
					var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
					var sign = that.height.storeHeader - that.height.headerBar;
					if(scrollTop < sign) {
						if(that.headerHide) {
							that.headerHide = false;
						}
					} else {
						if(!that.headerHide) {
							that.headerHide = true;
						}
					}
					if(that.template_page == 0) {
						var length = that.height.goodsList.length;
						for (var i = 0; i < length; i++) {
							var height1 = that.height.goodsList[i];
							var height2 = that.height.goodsList[i + 1];
							if (height2 && scrollTop >= height1 && scrollTop < height2) {
								if(i != that.tabActive) {
									that.tabActive = i;
									that._followScroll(i);
								}
							}
						}
					}
				});
			},

			onToggleSharePopupStatus() {
				var that = this;
				that.sharePopupStatus = !that.sharePopupStatus;
			},
			selectPinadan(pindan_id) {
				this.pindan_id = pindan_id;
				if(pindan_id > 0) {
					this.query.pindan_id = pindan_id;
					var url = this.util.getUrl({path: '/pages/store/goods', query: {sid: this.sid, cid: this.cid, order_id: this.order_id, pindan_id: pindan_id}});
					this.$router.replace(url);
				}
			},
			showMoveDotFun: function(showMoveDot, elLeft, elBottom) {
				this.showMoveDot.push(true);
				this.elLeft = elLeft;
				this.elBottom = elBottom;
			},

			onCollectCoupon() {
				this.util.request({
					url: 'wmall/channel/coupon/get',
					data: {sid: this.sid},
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.util.$toast('领取失败', '', 1000);
						return false;
					}
					this.couponStatus = 0;
					this.util.$toast(result.message, '', 1000);
				})
			},

			onParentSelectOption: function(goods) {
				this.goodsActive = goods;
				this.goods = goods;
				this.ParentPopupSpecShow = true;
				if(!this.goods['activeOptions']) {
					this.onParentGoodsOptionInit();
				}
			},

			onParentGoodsOptionInit: function() {
				this.goodsActive = this.goods;
				this.goodsActive['activeOptions'] = {
					option: 0,
					attrs: [],
					optionSelected: 0,
					attrsSelected: [],
					num: 0
				};
				if(this.goodsActive.is_options == 1) {
					this.goodsActive['activeOptions']['option'] = this.goodsActive.options[0]['id'];
					this.goodsActive['activeOptions']['optionSelected'] = this.goodsActive.options[0]['id'];
				}
				if(this.goodsActive.is_attrs == 1) {
					for(var i = 0; i < this.goodsActive.attrs.length; i++) {
						this.goodsActive['activeOptions']['attrs'].push(i + "s0");
						this.goodsActive['activeOptions']['attrsSelected'][i] = 0;
					}
				}
				this.onParentToggleActiveOption();
			},

			onParentToggleOption: function (optionId, optionValueID, type) {
				var goodsActive = this.goods;
				if(type == 'option') {
					goodsActive['activeOptions']['option'] = optionId;
					goodsActive['activeOptions']['optionSelected'] = optionId;
				} else {
					goodsActive['activeOptions']['attrs'][optionId] = optionId + 's' + optionValueID;
					goodsActive['activeOptions']['attrsSelected'][optionId] = optionValueID;
				}
				this.goodsActive = goodsActive;
				this.onParentToggleActiveOption();
			},

			onParentToggleActiveOption: function(option_key) {
				var goodsActive = this.goodsActive;
				if(!option_key) {
					goodsActive['activeOptionId'] = goodsActive['activeOptions']['option'];
					if(goodsActive['activeOptions']['attrs'].length > 0) {
						goodsActive['activeOptionId'] = goodsActive['activeOptionId'] + '_' + goodsActive['activeOptions']['attrs'].join('v')
					}
				} else {
					goodsActive['activeOptionId'] = option_key;
				}
				goodsActive['activeOption'] = goodsActive['options_data'][goodsActive['activeOptionId']];
				goodsActive['activeOption']['num'] = this.parentGetSpecNum();
				this.goodsActive = Object.assign({}, goodsActive)
			},

			parentGetSpecNum: function(){
				if(!this.icart|| !this.icart['data1'] || !this.icart['data1'][this.goodsActive.id]) {
					return 0;
				}
				var optionId = this.goodsActive.activeOptionId;
				if(!this.icart['data1'][this.goodsActive.id][optionId]) {
					return 0;
				}
				return this.icart['data1'][this.goodsActive.id][optionId]['num'];
			},

			onParentPlus: function(event, goods, optionId, from, buysvip_status) {
				this.goodsActive = goods;
				var buysvip = 0;
				if(this.icart.pindan_id == 0) {
					if(this.icart.is_buysvip == 1) {
						buysvip = 1;
					} else {
						if(this.icart.svip_buy_show >= 1 && goods.svip_buy_show == 1) {
							if(buysvip_status == 1) {
								if(this.goodsActive.activeOption) {
									if(this.goodsActive.activeOption.origin_price > 0) {
										this.goodsActive.origin_price = this.goodsActive.activeOption.origin_price;
									} else {
										this.goodsActive.origin_price = this.goodsActive.activeOption.price;
									}
									if(this.goodsActive.activeOption.svip_price > 0) {
										this.goodsActive.svip_price = this.goodsActive.activeOption.svip_price;
									} else {
										this.goodsActive.svip_price = this.goodsActive.activeOption.price;
									}
								}
								this.popupBuySvip = true;
								return;
							} else if(buysvip_status == 2) {
								this.popupBuySvip = false;
							} else if(buysvip_status == 3) {
								this.popupBuySvip = false;
								buysvip = 1;
							}
						}
					}
				}
				var goods_id = goods.id || goods.goods_id;
				var option_id = goods.activeOptionId || optionId;
				var params = {
					sid: this.istore.id,
					goods_id: goods_id,
					option_id: option_id,
					num: 1,
					sign: '+',
					is_buysvip: buysvip
				};
				this.util.request({
					url: 'wmall/store/goods/cart',
					//showLoading: true,
					data: params,
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					if(result.message.msg) {
						this.$toast(result.message.msg);
					}

					// 加入购物车动画
					let elLeft = event.target.getBoundingClientRect().left;
					let elBottom = event.target.getBoundingClientRect().bottom;
					if(from == 'cart'){
						elBottom = elBottom + 50;
					}
					this.elLeft = elLeft;
					this.elBottom = elBottom;
					this.showMoveDot.push(true);
					this.replaceCart(result.message.cart);
				})
			},

			onParentMinus: function(goods, optionId, from) {
				var buysvip = 0;
				if(this.icart.is_buysvip == 1) {
					buysvip = 1;
				}
				var goods_id = goods.id || goods.goods_id;
				var option_id = goods.activeOptionId || optionId;
				var params = {
					sid: this.istore.id,
					goods_id: goods_id,
					option_id: option_id,
					num: 1,
					sign: '-',
					is_buysvip: buysvip
				};
				this.util.request({
					url: 'wmall/store/goods/cart',
					//showLoading: true,
					data: params,
				}).then((result) => {
					var result = result.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return false;
					}
					this.replaceCart(result.message.cart);
				})
			},

			onParentPopupNotInSailTime: function(goods){
				this.goodsActive = goods;
				this.popupNotInSailTime = true;
			},

			onToggleCategory(cid, index, from) {
				var childIndex = 0;
				if(from == 'child') {
					childIndex = index;
					this.childSelectedIndex = childIndex;
					this.categoryAll[this.categorySelectedIndex]['childSelectedIndex'] = childIndex;
					this.categorySelected = this.categoryAll[this.categorySelectedIndex];
				} else {
					if(this.categoryAll[index]['childSelectedIndex'] > 0) {
						childIndex = this.categoryAll[index].childSelectedIndex;
					}
					this.categorySelected = this.categoryAll[index];
					this.categorySelectedIndex = index;
					this.childSelectedIndex = childIndex;
					this.categoryAll[index]['childSelectedIndex'] = childIndex;
				}
				this.categorySelectedId = this.categorySelected.id;
				this.childSelectedId = 0;
				if(this.categorySelected.child && this.categorySelected.child.length > 0) {
					this.childSelectedId = this.categorySelected.child[childIndex]['id'];
				}
				if(this.template < 4) {
					this.categoryScroll = new BScroll(this.$refs.categoryWrapper, {
						click: true
					});
				}
				window.scrollTo(0, 0);
				this.onGetGoods();
			},

			onGetGoods() {
				if(this.goodsLoading) {
					//return false;
				}
				if(!this.goodsAll[this.categorySelectedIndex]) {
					this.goodsAll[this.categorySelectedIndex] = [];
				}
				this.goodsItem = this.goodsAll[this.categorySelectedIndex][this.childSelectedIndex];
				if(!this.goodsItem) {
					this.goodsItem = {
						page: 1,
						psize: 30,
						empty: 0,
						loaded: false,
						data: [],
						orderby: {
							type: '',
							value: ''
						}
					};
				} else {
					if(this.goodsItem.empty) {
						return false;
					}
					if(this.goodsItem.loaded) {
						console.log('商品全部加载完成');
						return false;
					}
				}
				this.goodsLoading = true;
				this.util.request({
					url: 'wmall/store/goods/goods',
					data: {
						sid: this.sid,
						page: this.goodsItem.page,
						psize: this.goodsItem.psize,
						cid: this.categorySelectedId,
						child_id: this.childSelectedId,
						type: this.goodsItem.orderby.type,
						value: this.goodsItem.orderby.value
					},
				}).then((res) => {
					this.goodsLoading = false;
					var result = res.data.message.message;
					this.goodsItem.data = [...this.goodsItem.data, ...result.goods];
					this.goodsItem.page++;
					if(result.goods.length < this.goodsItem.psize) {
						this.goodsItem.loaded = true;
						if(!result.goods.length) {
							this.goodsItem.empty = 1;
						}
					}
					if(!this.goodsAll[this.categorySelectedIndex]) {
						this.goodsAll[this.categorySelectedIndex] = [];
					}
					this.goodsAll[this.categorySelectedIndex][this.childSelectedIndex] = this.goodsItem;
				})
			},

			onOrderby(type, value) {
				this.goodsItem = {
					page: 1,
					psize: 30,
					empty: 0,
					data: [],
					orderby: {
						type: type,
						value: value
					}
				};
				this.goodsAll[this.categorySelectedIndex][this.childSelectedIndex] = this.goodsItem;
				this.onGetGoods();
			},

			onLoad() {
				this.util.request({
					url: 'wmall/store/goods/newIndex',
					data: {
						sid: this.sid,
						cid: this.cid,
						order_id: this.order_id,
						code: this.code,
						note: this.note,
						type: this.commentActiveType,
						page: this.comments.page,
						psize: this.comments.psize,
					}
				}).then((res) => {
					this.goodsLoading = false;
					this.preLoading = false;
					let result = res.data.message;
					if(result.errno) {
						if(result.errno == -2) {
							this.util.$toast(result.message, this.util.getUrl({path: '/pages/home/index'}), 1500, 'replace');
							return false;
						}
						this.$toast(result.message);
						return false;
					}
					result = result.message;
					this.template_page = result.template_page;
					if(this.template_page == 1) {
						this.goodsItem = {
							page: 2,
							psize: 30,
							empty: 0,
							data: result.goods,
							orderby: {
								type: '',
								value: ''
							}
						};
						if(this.goodsItem.data.length < this.goodsItem.psize) {
							this.goodsItem.loaded = true;
							if(!this.goodsItem.data.length) {
								this.goodsItem.empty = 1;
							}
						}
						this.categorySelectedId = result.cid;
						this.childSelectedId = result.child_id;
						this.categorySelectedIndex = result.cindex;
						this.goodsAll[this.categorySelectedIndex] = [];
						this.goodsAll[this.categorySelectedIndex][this.childSelectedIndex] = this.goodsItem;
						this.categorySelected = result.category[this.categorySelectedIndex] || {};
					} else {
						this.cateHasGoods = result.cate_has_goods;
					}

					this.store = result.store;
					if(this.store.tips) {
						var storeNotice = 'storeNotice' + this.store.id;
						var storeNoticeStorage = this.util.getStorage(storeNotice);
						if(!storeNoticeStorage || (storeNoticeStorage && !storeNoticeStorage.notice)) {
							this.util.setStorage(storeNotice, {'notice': 1}, 300)
						} else {
							this.showNotice = false;
						}
					}
					if(result.store.data && result.store.data.shopPage) {
						this.shopPage = result.store.data.shopPage;
					}
					this.coupon = result.coupon;
					this.categoryAll = result.category;
					this.is_rest = result.store.is_rest;
					this.recommend_stores = result.recommend_stores;
					this.member_kabao = this.util.extend(this.member_kabao, result.member_kabao);

					this.template = result.template;
					this.lazyload_goods = result.lazyload_goods;
					this.follow = this.util.extend(this.follow, window.follow);
					this.util.setWXTitle(this.store.title)
					this.replaceStore(result.store);
					this.replaceCart(result.cart.message.cart);
					
					//评价数据
					var comments = result.comments;
					this.comments.data = [...this.comments.data, ...comments];
					if(!this.comments.data.length) {
						this.comments.empty = true;
					}
					if(comments && comments.length < this.comments.psize ) {
						this.comments.finished = true;
					}
					this.comments.page++;
					this.comments.loading = false;
					this.stat = this.util.extend(this.stat, result.stat);

					//快捷导航
					if(this.store.menu && this.store.menu.data) {
						this.menu = this.util.extend(this.menu, this.store.menu.data);
					}
					if(this.template < 4) {
						this.$nextTick(() => {
							this.onPageScroll();
						})
					}
				})
			},

			onParentBeforeEnter(el){
				el.style.bottom = this.windowHeight - this.elBottom + 'px';
				el.style.left = this.elLeft +5 + 'px';
				el.style.opacity = 0;
			},

			onParentAfterEnter(el){
				el.style.bottom = -20 + 'px';
				el.style.left = 20 + 'px';
				el.style.opacity = 1;
				setTimeout(() => {
					this.replaceState({key: 'cart_animate', val: true});
				}, 400)
			},

			selectCategory(index, event) {
				/*if (!event._constructed) {
					return;
				}*/
				let goodsListHeight = this.height.goodsList;
				let scrollTop = goodsListHeight[index];
				window.scrollTo(0, scrollTop);
			}
		},
		watch: {
			icart() {
				var cartgoods = this.icart.data;
				if(this.template_page == 1) {
					var categoryAllTemp = this.categoryAll;
					if(cartgoods) {
						for(var c in categoryAllTemp) {
							categoryAllTemp[c]['total'] = 0;
							if(categoryAllTemp[c]['child'] && categoryAllTemp[c]['child'].length > 0) {
								for(var index in categoryAllTemp[c]['child']) {
									categoryAllTemp[c]['child'][index]['total'] = 0;
									var childtotal = 0;
									var total = 0;
									for(var i in cartgoods) {
										for(var j in cartgoods[i]) {
											if(cartgoods[i][j]['cid'] == categoryAllTemp[c]['id']) {
												total += cartgoods[i][j]['num'];
												categoryAllTemp[c]['total'] = total;
												if(cartgoods[i][j]['child_id'] == categoryAllTemp[c]['child'][index]['id']) {
													childtotal += cartgoods[i][j]['num'];
													categoryAllTemp[c]['child'][index]['total'] = childtotal;
												}
											}
										}
									}
								}
							} else {
								var total = 0;
								for(var i in cartgoods) {
									for(var j in cartgoods[i]) {
										if(cartgoods[i][j]['cid'] == categoryAllTemp[c]['id']) {
											total += cartgoods[i][j]['num'];
											categoryAllTemp[c]['total'] = total;
										}
									}
								}
							}
						}
					} else {
						for(var c in categoryAllTemp) {
							categoryAllTemp[c]['total'] = 0;
							if(categoryAllTemp[c]['child']) {
								for(var index in categoryAllTemp[c]['child']) {
									categoryAllTemp[c]['child'][index]['total'] = 0;
								}
							}
						}
					}
					this.categoryAll = categoryAllTemp;
				} else {
					var cateHasGoods = this.cateHasGoods;
					if(cartgoods) {
						for(var x in cateHasGoods) {
							cateHasGoods[x]['total'] = 0;
							var total = 0;
							for(var i in cartgoods) {
								for(var j in cartgoods[i]) {
									if(cartgoods[i][j]['cid'] == cateHasGoods[x]['id']) {
										total += cartgoods[i][j]['num'];
										cateHasGoods[x]['total'] = total;
									}
								}
							}
						}
					} else {
						for(var x in cateHasGoods) {
							cateHasGoods[x]['total'] = 0;
						}
					}
					this.cateHasGoods = cateHasGoods;
				}
			},
			'$route' (to, from) {
				this.onLoad();
			}
		},
		created(){
			this.query = this.util.parseQuery(this.$route.query);
			if(this.query) {
				this.sid = this.query.sid;
				this.cid = this.query.cid;
				this.order_id = this.query.order_id;
				this.pindan_id = 0;
				if(this.query.pindan_id > 0) {
					this.pindan_id = parseInt(this.query.pindan_id);
				}
				if(this.query.code > 0) {
					this.code = this.query.code;
				}
			}
		},
		computed: {
			...mapState([
				'istore', 'icart',
			]),

			goodsSpecNum: function(){
				if(!this.icart|| !this.icart['data1'] || !this.icart['data1'][this.goodsActive.id]) {
					return 0;
				}
				var optionId = this.goodsActive.activeOptionId;
				if(!this.icart['data1'][this.goodsActive.id][optionId]) {
					return 0;
				}
				return this.icart['data1'][this.goodsActive.id][optionId]['num'];
			},

		},

		mounted () {
			this.onLoad();
			/*setTimeout(()=> {
				let bodyHeight = document.documentElement.clientHeight;
				let headerHeight = document.getElementById('store-header').clientHeight;
				let tabsHeight = document.getElementsByClassName('store-tabs')[0].clientHeight;
				this.vanColHeight = bodyHeight-headerHeight-tabsHeight-50;
				if(document.getElementById('public-header')) {
					let publicHeader = document.getElementById('public-header').clientHeight;
					this.vanColHeight = this.vanColHeight - publicHeader;
				}
				if(window.follow && window.follow.status == 1) {
					this.vanColHeight = this.vanColHeight - 56;
				}
				this.windowHeight = window.innerHeight;
			}, 200)*/

		}
	}
</script>

