		margin: 10px 0 0 0;
	}
	#store-goods .banner .banner-item:last-child{
		margin-bottom: 0px;
	}
	#store-goods .banner .banner-item img{
		width: 100%;
		height: 100%;
		border-radius: 4px;
		margin-bottom: -1px;
	}


	/*普通用户购买超级会员商品弹出层样式*/
	#store-goods .buy-svip{
		padding: 20px 15px;
		background-color: #fff;
		font-size: 15px;
		line-height: 1;
	}
	#store-goods .buy-svip .popup-svip-title{
		display: flex;
		align-items: center;
		font-weight: 700;
		white-space:nowrap;
		padding-bottom: 15px;
		text-align: center;
	}
	#store-goods .buy-svip .popup-svip-title:before,#store-goods .buy-svip .popup-svip-title:after{
		display: block;
		margin: 0 3.5px;
		width: 23px;
		height: 10px;
		background-image: url(../../../static/img/svip-title-head.png);
		background-position: 0 0;
		background-size: 100% auto;
		background-repeat: no-repeat;
		content: "";
	}
	#store-goods .buy-svip .popup-svip-title:after{
		transform: scaleX(-1);
	}
	#store-goods .buy-svip .svip-img{
		display: flex;
		width: 100%;
		height: 73px;
		background: url(../../../static/img/svip-vs-bg.png) no-repeat;
		background-size: 100% auto;
		margin-bottom: 15px;
	}
	#store-goods .buy-svip .svip-img .svip-vs-left{
		width: 50%;
		text-align: center;
		color: #FFE98B;
	}
	#store-goods .buy-svip .svip-img .svip-vs-left .icon{
		padding: 3px 0;
		font-size: 12px;
	}
	#store-goods .buy-svip .svip-img .svip-vs-left .svip-vs-price{
		font-size: 26px;
		padding: 5px 0;
	}
	#store-goods .buy-svip .svip-img .svip-vs-right{
		width: 50%;
		text-align: center;
		color: #656565;
	}
	#store-goods .buy-svip .svip-img .svip-vs-right .svip-putong-title{
		padding-top: 18px;
		font-size: 12px;
	}
	#store-goods .buy-svip .svip-img .svip-vs-right .svip-putong-price{
		font-size: 26px;
		padding: 5px 0;
	}
	#store-goods .buy-svip .buy-svip-text{
		font-size: 14px;
		color: #999;
		text-align: center;
	}
	#store-goods .buy-svip .svip-price-btn{
		display: block;
		width: 100%;
		height: 45px;
		text-align: center;
		background: linear-gradient(90deg,#faeaa7,#e0c470);
		border: none;
		border-radius: 3px;
		padding: 0;
		color: #7C4F0B;
	}
	#store-goods .buy-svip .price-btn{
		width: 100%;
		display: block;
		text-align: center;
		color: #7c7c7c;
		font-weight: 600;
	}
	#store-goods .buy-svip .van-button::before{
		border-color: transparent;
		background-color: transparent;
	}

	/*顶部商品一二级分类*/
	#store-goods .container{
		padding-bottom: 68px;
		padding-top: 97px;
	}
	#store-goods .container.has-child-4{
		padding-top: 151px;
	}
	#store-goods .container.has-child-8{
		padding-top: 205px;
	}
	#store-goods #public-header~.container{
		padding-bottom: 68px;
		padding-top: 143px;
	}
	#store-goods #public-header~.container.has-child-4{
		padding-top: 197px;
	}
	#store-goods #public-header~.container.has-child-8{
		padding-top: 251px;
	}
	#store-goods .container .category-container{
		position: fixed;
		top: 0px;
		right: 0;
		left: 0;
		z-index: 1;
	}
	#store-goods #public-header~.container .category-container{
		top: 46px;
	}
	#store-goods .container .category-container .cate-item{
		display: flex;
		flex-flow: column nowrap;
		justify-content: space-between;
		align-items: center;
		padding: 10px 0;
	}
	#store-goods .container .category-container .cate-item .img-wrap{
		width: 50px;
		height: 50px;
		font-size: 0px;
		border-radius: 50%;
		overflow: hidden;
		transition: transform 0.5s;
	}
	#store-goods .container .category-container .cate-item .cate-title{
		width: 100%;
		padding: 0px 5px;
		font-size: 14px;
		text-align: center;
		margin-top: 10px;
	}
	#store-goods .container .category-container .cate-item.active .img-wrap{
		transform: scale(1.2);
	}
	#store-goods .container .category-container .cate-item.active .cate-title{
		color: #ff2d4b;
		font-weight: 600;
	}
	#store-goods .container .category-container .category-children{
		width: 100%;
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		padding: 5px;
		max-height: 98px;
		overflow: hidden;
	}
	#store-goods .container .category-container .category-swipe-item{
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		padding: 5px;
	}
	#store-goods .container .category-container .cate-child-item{
		width: 25%;
		padding: 5px;
	}
	#store-goods .container .category-container .cate-child-item .child-inner{
		width: 100%;
		padding: 10px 5px;
		background-color: #f5f5f5;
		border-radius: 3px;
		text-align: center;
		font-size: 14px;
	}
	#store-goods .container .category-container .cate-child-item .child-inner.active{
		background-color: rgba(255,45,75,0.1);
		color: #ff2d4b;
	}
	#store-goods .diy-goods-single-row #goods-handle .foodop.no-sail{
		bottom: 6px;
	}
	/*门店会员价格*/
	#store-goods .kabao-price-wrap{
		display: flex;
		align-items: center;
		height: 18px;
		margin-top: 5px;
		font-size: 14px;
		line-height: 1;
	}
	#store-goods .kabao-price-wrap .kabao-price{
		font-weight: bold;
		font-size: 16px;
		color: #705A41;
	}
	#store-goods .kabao-price-wrap .kabao-label{
		position: relative;
		display: flex;
		align-items: center;
		padding: 2px 5px;
		margin-left: 5px;
		border-radius: 0 3px 3px 3px;
		font-size: 12px;
		background-color: #705A41;
		color: #D6B9A3;
	}
	#store-goods .kabao-price-wrap .kabao-label:before{
		content: '';
		position: absolute;
		top: 0px;
		left: -4px;
		width: 0;
		height: 0;
		border-width: 5px;
		border-style: solid;
		border-color: #705A41 transparent transparent transparent;
	}
	#store-goods  .height-50{
		display: none;
	}
	.theme-card #store-goods #public-header.public-header{
		display: none;
	}
</style>



